<template>
    <div class="page-buy">
        <div class="price-part">
            <span>商品总价</span>
            <span class="price">￥{{$store.getters.price}}</span>
        </div>
        <div class="btns">
            <span class="btn-buy" @touchend="buyItem">立即购买</span>
            <span class="btn-cancel" @touchend="$router.back()">取消购买</span>
        </div>
        <h2 class="list-title">购买商品</h2>
        <!-- <Product v-for="item in $store.state.products" :key="item._id" :data="item"></Product>
        <Product v-for="item in $store.getters.product" :key="item._id" :data="item"></Product> -->
        <!-- 商品列表 -->
        <div class="itemlist">
            <p v-for="item in $store.state.products" :key="item._id">
                <span class="title">{{item.title}}</span>
                <span class="btn" @touchend="add(item)">+</span>
                <span class="btn num">{{item.count}}</span>
                <span class="btn" @touchend="delet(item)">-</span>
            </p>
        </div>
    </div>
</template>
<style lang="scss" scoped>
@import '@/common.scss';

.page-buy {
    .price-part {
        background-color: #fff;
        padding: 30px 10px 10px;
        font-size: 18px;

        .price {
            font-size: 30px;
            color: #f30;
        }
    }

    .btns {
        background-color: #fff;
        text-align: center;
        padding: 15px 0 30px;

        span {
            padding: 5px 10px;
            font-size: 12px;
            color: #fff;
            background: #aaa;
            border-radius: 5px;
            margin: 0 10px;
        }

        .btn-buy {
            background-color: #f30;
        }
    }

    .list-title {
        background-color: #fff;
        padding: 10px;
        font-size: 24px;
        font-weight: normal;
    }

    //列表
    .itemlist {
        text-align: center;
        background: #fff;

        p {
            display: flex;

            .title {
                width: 80%;
            }

            .btn {
                text-align: center;
                display: inline-block;
                width: 20px;
                height: 20px;
                line-height: 20px;
                background: $color;
                margin-left: 2px;
                color:#fff;
                border-radius: 10px;
            }

            .num {
                background: #fff;
                color:#333;
            }
        }


    }
}
</style>
<script>
// import Product from '@/components/Product';
export default {
    // components: { Product },
    created() {
        if (!this.$store.state.products.length) {
            this.$router.back();
        }
    },
    data() {
        return {
            data: {},
        }
    },
    methods: {
        buyItem() {
            this.$http
                .post('/data/buy', { ids: this.$store.state.products.map(item => item._id) })
        },
        add(item) {
            // console.log(this.data)
            this.$store.commit('addItem', item);
            // console.log(this.$store.state.products)
        },
        delet(item) {
            this.$store.commit('deleteItem', item);
        }
    }

}
</script>